.album_cover_wrapper {
    position: relative;
}

.album_name_layer {
    visibility: hidden;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    opacity: 0;
    background-color: #3f454e;
    transition: opacity .2s, visibility .2s;
    align-items: center;
    justify-content: center;
    display: flex;
}

.album_cover_wrapper:hover .album_name_layer {
    visibility: visible;
    opacity: 0.8;
}

.album_name {
    justify-content: center;
    color: whitesmoke;
    text-align: center;
    transition: .2s;
    transform: translateY(1em);
    font-size: larger;
}

.album_cover_wrapper:hover .album_name {
    transform: translateY(0);
}